import queryParams from '../../libs/function/queryParams.js';
/**
 * 
 * :
 */
function route(options = {}, params = false) {
	let config = {
		type: 'navigateTo',
		url: '',
		delta: 1, // navigateBack,
		params: {}, // 
		animationType: 'pop-in', // ,APP
		animationDuration: 300, // ,,APP
	};
	config = Object.assign(config, options);
	// url"/"uni"/"
	if (config.url[0] != '/') config.url = '/' + config.url;
	// ,Object.keys,switchTab
	if (Object.keys(config.params).length && config.type != 'switchTab') {
		// url
		// "/","?","="/page/index/index?name=mary"
		// urlget"?"
		let query = '';
		if (/.*\/.*\?.*=.*/.test(config.url)) {
			// objectget
			query = queryParams(config.params, false);
			// get,"&"
			config.url += "&" + query;
		} else {
			query = queryParams(config.params);
			config.url += query;
		}
	}
	// url
	if (typeof options === 'string' && typeof params == 'object') {
		let query = '';
		if (/.*\/.*\?.*=.*/.test(options)) {
			// objectget
			query = queryParams(params, false);
			// get,"&"
			options += "&" + query;
		} else {
			query = queryParams(params);
			options += query;
		}
	}
	// ()
	// 
	if (typeof options === 'string') {
		if (options[0] != '/') options = '/' + options;
		return uni.navigateTo({
			url: options
		});
	}
	// navigateTo
	if (config.type == 'navigateTo' || config.type == 'to') {
		return uni.navigateTo({
			url: config.url,
			animationType: config.animationType,
			animationDuration: config.animationDuration,
		});
	}
	if (config.type == 'redirectTo' || config.type == 'redirect') {
		return uni.redirectTo({
			url: config.url,
		});
	}
	if (config.type == 'switchTab' || config.type == 'tab') {
		return uni.switchTab({
			url: config.url,
		});
	}
	if (config.type == 'reLaunch') {
		return uni.reLaunch({
			url: config.url
		});
	}
	if (config.type == 'navigateBack' || config.type == 'back') {
		return uni.navigateBack({
			delta: parseInt(config.delta ? config.delta : this.delta)
		});
	}
}

export default route;
